@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

a {
  text-decoration: none;
  cursor: pointer !important;
}

#root {
  background: #E5E5E5;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.city_blue {
  color: #605DEC;
}

.h1 {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 72px;
  line-height: 80px;
  text-align: center;
  color: #FFFFFF;
}

.h2 {
  margin: 0 auto;
  font-weight: 400;
  font-size: 24px;
  line-height: 40px;
  text-align: center;
  color: #FFFFFF;
}

.section {
  padding: 60px 0 30px 0;
}
