.footer_list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0;
  margin: 0;
  padding: 85px 120px 60px 120px;
}

@media (max-width: 1200px) {
  .footer_list {
    padding: 60px 30px;
  }
  .footer_list #footer_logo_up {
    display: none;
  }
}

.footer_list li {
  list-style-type: none;
}

@media (max-width: 410px) {
  .footer_list .header_list {
    width: 100% !important;
    text-align: center;
  }
}

.footer_list_items {
  margin: 0;
  padding: 0;
}

.footer_list_items li {
  margin: 16px 0;
}

.footer_list_items li a {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #7C8DB0;
}

.footer_list_items li a:hover {
  color: #384652;
}

.footer_list_name {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #6E7491;
  margin-top: 10px;
}

.footer_bottom {
  border-top: 1px solid #CBD4E6;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 17px 120px 55px 120px;
}

@media (max-width: 992px) {
  .footer_bottom {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
}

@media (max-width: 470px) {
  .footer_bottom {
    padding: 20px 0 120px 0;
  }
}

.footer_bottom .social_icon:nth-child(2) {
  margin: 0 20px;
}

.footer_bottom .politic {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  text-align: right;
  color: #7C8DB0;
}

@media (max-width: 992px) {
  .footer_bottom .politic {
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
}
