.villasList{
    padding: 0;
    margin: 30px 15px;
    box-shadow: 0px 2px 4px rgba(28, 5, 77, 0.1), 0px 12px 32px rgba(0, 0, 0, 0.05);
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    width: 300px;
    height: 325px;
}

.villasList:first-child{
    margin-left: 0;
}

.villasList:last-child{
    margin-right: 0;
}

.villasList-header{
    width: 100%;
    height: 240px;
    position: relative;
    overflow: hidden;

}

.villasList-header-img{
    height: 100%;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
}

.villasList-body{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 16px 24px;
    background: #FFFFFF;
}

.villasList-body-title{
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #6E7491;
    
}

.villasList-body-price{
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    text-align: right;
    color: #6E7491;
}

.villasList-body-subtitle{
    font-weight: 400;
    font-size: 15px;
    line-height: 22px;
    color: #7C8DB0;
    margin-top: 10px;
    
}

.villasList-body-data{
    font-weight: 400;
    font-size: 13px;
    line-height: 20px;
    color: #7C8DB0;
    margin-top: 10px;
}