$city_blue : #605DEC;
$nav_logo : #151515;
$nav_links : #384652;
$nav_links_active : #384652; 
$footer_links: #6E7491;
$main_bg: #E5E5E5;
$main-white: #FFFFFF;
$main-header-search-btn-title: #7B61FF;
$main-header-search-btn-bg: rgba(123, 97, 255, 0.1);
$main-header-search-btn-bg-active: rgba(123, 97, 255, 0.3);



.header{
    position: relative;
    max-width: 1920px;
    margin: 0 auto;
    padding: 180px 0;
    .mainHeaderCarusel{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 0;
        background: rgba(0, 0, 0, 0.3);
        mix-blend-mode: multiply;   
        border-radius: 0px 0px 15px 15px;
        overflow: hidden;
        .carousel-inner{
            width: 100%;
            height: 100%;
            .carousel-item{
                height: 100%;
                transition: transform 4s ease, opacity 1.5s ease-out;
                .main_header_slide_img{
                    position: absolute;
                    width: 100%;
                    left: 0;
                    bottom: 0;
                }
            }
        }
    }
    .container{
        position: relative;

    }
    
    .h2{
        margin-top: 20px;
    }
    

    .main-header-search{
        margin-top: 43px;
        &-head{
            background: $main-white;
            border-radius: 10px 10px 0px 0px;
            padding:  16px 32px;
            max-width: 428px;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            &-btn{
                padding: 10px;
                background: transparent;
                border-radius: 8px;
                border: none;
                font-weight: 500;
                font-size: 18px;
                line-height: 27px;
                text-align: center;
                color: $main-header-search-btn-title;
                &:hover{
                    background: $main-header-search-btn-bg;
                    transition: all .5s;
                }
                .active{
                    background: $main-header-search-btn-bg-active;
                }
            }
        }

        &-body{
            background: $main-white;
            box-shadow: 0px 24px 50px rgba(0, 0, 0, 0.1);
            border-radius: 10px;
            padding: 32px 60px;
            display: flex;
            align-items: center;
            &-list{
                display: flex;
                justify-content: space-between;

            }
        }
    }
}