$city_blue : #605DEC;
$nav_logo : #151515;
$nav_links : #9BA2A8;
$nav_links_active : #384652;;

.navbar{
    padding: 21px 0;
    background: #FFFFFF;
    max-width: 1920px;
    margin: 0 auto;
    .city_blue{
        font-weight: 700;
    }
    .container-fluid{
        padding: 0 30px;
    }
    &-brand{
        color: $nav_logo;
        font-weight: 500;
        font-size: 24px;
        line-height: 36px;
        &:hover{
            color: $nav_logo;
        }
    }
    .navbar-nav{
        margin: 0;
        margin-left: auto !important;
        margin-right: 0 !important;
        padding: 0 30px;
        
        .nav-item{
            padding: 0 28px;
            @media (max-width: 1200px) {
                padding: 0 15px;
            }
            @media (max-width: 992px) {
                padding: 10px 0;
            }
            
            .nav-link{
                padding: 0;
                padding-right: 0;
                padding-left: 0;
                font-weight: 400;
                font-size: 18px;
                line-height: 27px;
                color:  $nav_links;
                @media (max-width: 1200px) {
                    font-size: 16px;
                }
                @media (max-width: 992px) {
                    font-size: 20px;
                    text-align: center;
                    padding: 0;
                    width: auto;
                }
            }
            .active{
                color: $nav_links_active;
            }
            &:last-child{
                padding: 0 0 0 28px;
                @media (max-width: 1200px) {
                    padding: 0 0 0 15px;
                }
                @media (max-width: 992px) {
                    padding: 10px 0;
                }
            }
        }
    }
    .navbar-toggler{
        box-shadow: none !important;
        background: #5D5FEF;
        border-radius: 5px;
        padding: 5px 7px;
        .navbar-toggler-icon{
            width: auto;
            height: auto;
        }
    }
    .MyNavBtn_myBtn__aFQx9{
        display: block;
        margin: 0 auto;
    }
}
