.villas{
    width: 100%;
    position: relative;
}

.villas-header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin: 0;
}
.villas-header-title{
    font-weight: 700;
    font-size: 24px;
    line-height: 33px;
    color: #6E7491;
}

.villas-header-btn{
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #A1B0CC;
    transition: transform .2s;
}
.villas-header-btn:hover
{
    color: #A1B0CC;
    transform: scale(1.07);
    transition: transform .2s;    
}


.villas-body{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
}