@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');


$city_blue : #605DEC;
$nav_logo : #151515;
$nav_links : #384652;
$nav_links_active : #384652; 
$footer_links: #6E7491;
$main_bg: #E5E5E5;
$main-white: #FFFFFF;
$main-header-search-btn :#7B61FF;


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    
}

a{
    text-decoration: none;
    cursor: pointer !important;

}

#root{
    background: $main_bg;
}

ul{
    padding: 0;
    margin: 0;
    li{
        padding: 0;
        margin: 0;
        list-style-type: none;
        
    }
}


.city_blue{
    color: $city_blue;
}

.h1{
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-size: 72px;
    line-height: 80px;
    text-align: center;
    color: #FFFFFF;
}

.h2{
    margin: 0 auto;
    font-weight: 400;
    font-size: 24px;
    line-height: 40px;
    text-align: center;
    color: #FFFFFF;
    
}


.section{
    padding: 60px 0 30px 0;
}