.header {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  padding: 180px 0;
}

.header .mainHeaderCarusel {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 0;
  background: rgba(0, 0, 0, 0.3);
  mix-blend-mode: multiply;
  border-radius: 0px 0px 15px 15px;
  overflow: hidden;
}

.header .mainHeaderCarusel .carousel-inner {
  width: 100%;
  height: 100%;
}

.header .mainHeaderCarusel .carousel-inner .carousel-item {
  height: 100%;
  -webkit-transition: opacity 1.5s ease-out, -webkit-transform 4s ease;
  transition: opacity 1.5s ease-out, -webkit-transform 4s ease;
  transition: transform 4s ease, opacity 1.5s ease-out;
  transition: transform 4s ease, opacity 1.5s ease-out, -webkit-transform 4s ease;
}

.header .mainHeaderCarusel .carousel-inner .carousel-item .main_header_slide_img {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
}

.header .container {
  position: relative;
}

.header .h2 {
  margin-top: 20px;
}

.header .main-header-search {
  margin-top: 43px;
}

.header .main-header-search-head {
  background: #FFFFFF;
  border-radius: 10px 10px 0px 0px;
  padding: 16px 32px;
  max-width: 428px;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.header .main-header-search-head-btn {
  padding: 10px;
  background: transparent;
  border-radius: 8px;
  border: none;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  color: #7B61FF;
}

.header .main-header-search-head-btn:hover {
  background: rgba(123, 97, 255, 0.1);
  -webkit-transition: all .5s;
  transition: all .5s;
}

.header .main-header-search-head-btn .active {
  background: rgba(123, 97, 255, 0.3);
}

.header .main-header-search-body {
  background: #FFFFFF;
  -webkit-box-shadow: 0px 24px 50px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 24px 50px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 32px 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.header .main-header-search-body-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
