$city_blue : #605DEC;
$nav_logo : #151515;
$nav_links : #384652;
$nav_links_active : #384652; 
$footer_links: #6E7491;

.footer_list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    padding: 85px 120px 60px 120px;
    @media (max-width: 1200px) {
        padding: 60px 30px;
        #footer_logo_up{
            display: none;  
        }
    }
    
    
    li{
        list-style-type: none;
    }
    .header_list{
        @media (max-width: 410px) {
            width: 100% !important;
            text-align: center;
        }
    }
    &_items{
        margin: 0;
        padding: 0;
        li{
            margin: 16px 0;
            a{
                font-weight: 400;
                font-size: 16px;
                line-height: 22px;
                color: #7C8DB0;
                &:hover{
                    color: $nav_links;
                }
            }
            
        }
        
        
    }
    &_name{
        font-weight: 600;
        font-size: 18px;
        line-height: 25px;
        color: $footer_links;
        margin-top: 10px;
    }
}

.footer_bottom{
    border-top: 1px solid #CBD4E6;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    padding: 17px 120px 55px 120px;
    @media (max-width: 992px) {
        justify-content: center;
    }
    @media (max-width: 470px) {
        padding: 20px 0 120px 0;
    }
    .social_icon:nth-child(2){
        margin: 0 20px;
    }
    .politic{
        font-weight: 400;
        font-size: 16px;
        line-height: 25px;
        text-align: right;
        color: #7C8DB0;
        @media (max-width: 992px) {
            width: 100%;
            text-align: center;
            margin-top: 30px;
        }
    }
}