.bonusList{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    height: 750px;
    padding: 100px 0;
}

.bonusList-block{
    height: 100%;
    padding: 10px;
}

.bonusList-block-item{
    height: 100%;
    position: relative;
    background: rgba(0, 0, 0, 0.4);
    mix-blend-mode: multiply;
    border-radius: 21px;
    
}

.second .bonusList-block-item{
    height: 50%;
}




