.navbar {
  padding: 21px 0;
  background: #FFFFFF;
  max-width: 1920px;
  margin: 0 auto;
}

.navbar .city_blue {
  font-weight: 700;
}

.navbar .container-fluid {
  padding: 0 30px;
}

.navbar-brand {
  color: #151515;
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
}

.navbar-brand:hover {
  color: #151515;
}

.navbar .navbar-nav {
  margin: 0;
  margin-left: auto !important;
  margin-right: 0 !important;
  padding: 0 30px;
}

.navbar .navbar-nav .nav-item {
  padding: 0 28px;
}

@media (max-width: 1200px) {
  .navbar .navbar-nav .nav-item {
    padding: 0 15px;
  }
}

@media (max-width: 992px) {
  .navbar .navbar-nav .nav-item {
    padding: 10px 0;
  }
}

.navbar .navbar-nav .nav-item .nav-link {
  padding: 0;
  padding-right: 0;
  padding-left: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #9BA2A8;
}

@media (max-width: 1200px) {
  .navbar .navbar-nav .nav-item .nav-link {
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  .navbar .navbar-nav .nav-item .nav-link {
    font-size: 20px;
    text-align: center;
    padding: 0;
    width: auto;
  }
}

.navbar .navbar-nav .nav-item .active {
  color: #384652;
}

.navbar .navbar-nav .nav-item:last-child {
  padding: 0 0 0 28px;
}

@media (max-width: 1200px) {
  .navbar .navbar-nav .nav-item:last-child {
    padding: 0 0 0 15px;
  }
}

@media (max-width: 992px) {
  .navbar .navbar-nav .nav-item:last-child {
    padding: 10px 0;
  }
}

.navbar .navbar-toggler {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  background: #5D5FEF;
  border-radius: 5px;
  padding: 5px 7px;
}

.navbar .navbar-toggler .navbar-toggler-icon {
  width: auto;
  height: auto;
}

.navbar .MyNavBtn_myBtn__aFQx9 {
  display: block;
  margin: 0 auto;
}
